import React, { useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { useEffect } from "react";

export default function ListJobs() {
  const [Jobs, setJobs] = useState([]);
  const getUser = async () => {
    try {
      const response = await axios.get(
        "https://647e3d68af984710854b1627.mockapi.io/jobs"
      );
      setJobs(response.data);
    } catch (error) {
      console.log(error);
    }
  };

  const deleteJob = async (id) => {
    try {
      await axios.delete(
        `https://647e3d68af984710854b1627.mockapi.io/jobs/${id}`
      );
      getUser();
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getUser();
  }, []);

  return (
    <div>
      <button
        className="btnback"
        style={{ backgroundColor: "white", marginTop: "20px" }}
      >
        <h4>
          <Link to="/employMain" style={{ textDecoration: "none" }}>
            <i class="fas fa-solid fa-arrow-left"></i> Back to employ screen
          </Link>
        </h4>
      </button>
      <h1>List of jobs</h1>
      <table style={{ width: "100%" }}>
        <tr>
          <th></th>
          <th></th>
          <th>Name</th>
          <th>Description</th>
          <th>Price</th>
          <th>Action</th>
        </tr>
        {Jobs.map((book, index) => (
          <tr key={book.id}>
            <td style={{ width: "1px" }}>{index + 1}</td>
            <td>
              <img src={book.jobPhotoUrl} alt="" height="100px" width="100px" />
            </td>
            <td>
              <h3>{book.name}</h3>
            </td>
            <td>
              <p>{book.description}</p>
            </td>
            <td>
              <span>{book.price}$</span>
            </td>
            <td>
              <button className="delete" onClick={() => deleteJob(book.id)}>
                Delete
              </button>
              <button className="update">
                <Link to={"/postJob"} style={{ textDecoration: "none" }}>
                  Update
                </Link>
              </button>
            </td>
          </tr>
        ))}
      </table>
    </div>
  );
}
